* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todo-list {
  border: 1px solid rgb(160, 160, 160);
  border-radius: 5px;
  padding: 30px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

form input {
  margin-bottom: 15px;
  height: 30px;
  width: 250px;
  padding: 3px;
}

form button {
  padding: 7px 10px;
  background-color: rgb(38, 201, 20);
  border: none;
  color: white;
  border-radius: 3px;
  cursor: pointer;
}

form button:hover {
  background-color: rgb(43, 227, 22);
}

.todo-list ul {
  text-align: start;
}

.todo-list p {
  margin-top: 50px;
  font-family: monospace;
}